exports.push([module.id, "\n.ellipsis[data-v-0872ace8] {\r\n  width: 100px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 2;\r\n  margin: 0 auto;\r\n  text-align: center;\n}\n.search[data-v-0872ace8] {\r\n  width: 15%;\r\n  margin-right: 10px;\n}\n.region[data-v-0872ace8] {\r\n  width: 10%;\r\n  margin: 0 10px;\n}\n.img[data-v-0872ace8] {\r\n  width: 70px;\r\n  height: 70px;\n}\n.images[data-v-0872ace8] {\r\n  width: 100px;\r\n  height: 100px;\r\n  margin-right: 10px;\r\n  vertical-align: top;\n}\n.laybox2 ul[data-v-0872ace8] {\r\n  margin: 33px auto;\r\n  border-bottom: 1px dashed #04bbfc;\n}\n.laybox2[data-v-0872ace8] {\r\n  padding: 60px;\r\n  font-size: 16px;\n}\n.laybox2 li[data-v-0872ace8] {\r\n  width: 300px;\r\n  text-align: left;\r\n  margin: 33px auto;\n}\n.t-c[data-v-0872ace8]:after {\r\n  content: \"\";\r\n  display: block;\r\n  height: 0;\r\n  clear: both;\n}\n.bot > div[data-v-0872ace8] {\r\n  position: relative;\n}\ndiv.bot[data-v-0872ace8] {\r\n  margin: 45px auto 35px auto;\r\n  height: 100px;\n}\ndiv.bot span[data-v-0872ace8] {\r\n  display: inline-block;\r\n  width: 20%;\n}\ndiv.bot input[data-v-0872ace8] {\r\n  display: inline-block;\r\n  width: 55%;\r\n  outline: none;\r\n  border: none;\r\n  border-bottom: 1px solid #aaa;\n}\ndiv.bot button[data-v-0872ace8] {\r\n  width: 110px;\r\n  height: 36px;\r\n  margin-left: -55px;\r\n  position: absolute;\r\n  border-radius: 18px;\r\n  -webkit-box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\r\n          box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\r\n  border: none;\r\n  color: #fff;\r\n  left: 50%;\r\n  top: 60px;\n}\nbutton.reject[data-v-0872ace8] {\r\n  background: rgba(237, 81, 55, 1);\r\n  margin-right: 30%;\n}\nbutton.pass[data-v-0872ace8] {\r\n  background-color: #20a0ff;\n}\n.left[data-v-0872ace8] {\r\n  float: left;\r\n  width: 50%;\n}\n.right[data-v-0872ace8] {\r\n  float: right;\r\n  width: 50%;\n}\n.nowrap[data-v-0872ace8] {\r\n  width: 120px !important;\r\n  text-align: center;\r\n  white-space: nowrap; /*规定文本不换行**/\r\n  text-overflow: ellipsis; /**显示省略符号来代表被修剪的文本。*/\r\n  overflow: hidden;\r\n  margin: 0 auto;\n}\n.regionDetail[data-v-0872ace8] .el-dialog__title {\r\n  font-size: 22px;\n}\n.regionDetail[data-v-0872ace8] .el-dialog__body {\r\n  padding: 0px 25px 30px;\r\n  border-top: solid 2px #eee;\n}\r\n", "", {"version":3,"sources":["F:/仲达集团/2020-3-18总后台管理系统/src/pages/audit/channel/src/pages/audit/channel/twiceReview.vue"],"names":[],"mappings":";AA+hBA;EACA,aAAA;EACA,iBAAA;EACA,wBAAA;EACA,qBAAA;EACA,6BAAA;EACA,sBAAA;EACA,eAAA;EACA,mBAAA;CACA;AACA;EACA,WAAA;EACA,mBAAA;CACA;AACA;EACA,WAAA;EACA,eAAA;CACA;AACA;EACA,YAAA;EACA,aAAA;CACA;AACA;EACA,aAAA;EACA,cAAA;EACA,mBAAA;EACA,oBAAA;CACA;AACA;EACA,kBAAA;EACA,kCAAA;CACA;AAEA;EACA,cAAA;EACA,gBAAA;CACA;AAEA;EACA,aAAA;EACA,iBAAA;EACA,kBAAA;CACA;AAEA;EACA,YAAA;EACA,eAAA;EACA,UAAA;EACA,YAAA;CACA;AAEA;EACA,mBAAA;CACA;AAEA;EACA,4BAAA;EACA,cAAA;CACA;AAEA;EACA,sBAAA;EACA,WAAA;CACA;AAEA;EACA,sBAAA;EACA,WAAA;EACA,cAAA;EACA,aAAA;EACA,8BAAA;CACA;AAEA;EACA,aAAA;EACA,aAAA;EACA,mBAAA;EACA,mBAAA;EACA,oBAAA;EACA,uDAAA;UAAA,+CAAA;EACA,aAAA;EACA,YAAA;EACA,UAAA;EACA,UAAA;CACA;AAEA;EACA,iCAAA;EACA,kBAAA;CACA;AAEA;EACA,0BAAA;CACA;AAEA;EACA,YAAA;EACA,WAAA;CACA;AACA;EACA,aAAA;EACA,WAAA;CACA;AACA;EACA,wBAAA;EACA,mBAAA;EACA,oBAAA,CAAA,YAAA;EACA,wBAAA,CAAA,qBAAA;EACA,iBAAA;EACA,eAAA;CACA;AACA;EACA,gBAAA;CACA;AACA;EACA,uBAAA;EACA,2BAAA;CACA","file":"twiceReview.vue","sourcesContent":["<template>\r\n  <div>\r\n    <ctbHead>\r\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\r\n                   v-for=\"item in authList\"\r\n                   :key=\"item.id\"\r\n                   :to=\"{path:item.action,query:{id:curId}}\"\r\n                   :v-key=\"item.id\">\r\n        {{item.name}}\r\n      </router-link>\r\n    </ctbHead>\r\n    <div class=\"container\">\r\n      <div class=\"quote\">\r\n        <div class=\"quote-ele\"><i></i>审核-免费保养-二次复核</div>\r\n        <div class=\"quote-nav\">\r\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\r\n                       v-for=\"item in threeAuthList\"\r\n                       :key=\"item.id\"\r\n                       :to=\"{path:item.action,query:{id:curId}}\">\r\n            {{item.name}}\r\n          </router-link>\r\n        </div>\r\n      </div>\r\n      <div style=\"margin:0 20px\">\r\n        <!-- <el-input placeholder=\"请输入维修厂\"\r\n                  v-model=\"ListQuery.company\"\r\n                  class=\"input-with-select search\">\r\n          <el-button slot=\"append\"\r\n                     icon=\"el-icon-search\"\r\n                     @click=\"page=1,init()\"></el-button>\r\n        </el-input> -->\r\n        <el-select style=\"margin-right:10px\"\r\n                   v-model=\"ListQuery.ucp_company\"\r\n                   placeholder=\"请选择保险公司\"\r\n                   @change=\"page=1,init()\">\r\n          <el-option v-for=\"(item,index) in companyList\"\r\n                     :key=\"index\"\r\n                     :label=\"item.company\"\r\n                     :value=\"item.id\"></el-option>\r\n        </el-select>\r\n        <el-date-picker v-model=\"pickerSearch\"\r\n                        type=\"daterange\"\r\n                        style=\"width:24%;margin-right:10px\"\r\n                        @change=\"page=1,init()\"\r\n                        value-format=\"yyyy-MM-dd\"\r\n                        range-separator=\"至\"\r\n                        start-placeholder=\"开始日期\"\r\n                        end-placeholder=\"结束日期\">\r\n        </el-date-picker>\r\n        省:\r\n        <el-select v-model=\"ListQuery.search['province']\"\r\n                   placeholder=\"请选择省\"\r\n                   class=\"region\"\r\n                   @change=\"getCity\">\r\n          <el-option v-for=\"item in provincelist\"\r\n                     :key=\"item.id\"\r\n                     :label=\"item.name\"\r\n                     :value=\"item.id\">\r\n          </el-option>\r\n        </el-select>\r\n        市:\r\n        <el-select v-model=\"ListQuery.search['city']\"\r\n                   placeholder=\"请选择市\"\r\n                   class=\"region\"\r\n                   @change=\"getCounty\">\r\n          <el-option v-for=\"item in citylist\"\r\n                     :key=\"item.id\"\r\n                     :label=\"item.name\"\r\n                     :value=\"item.id\">\r\n          </el-option>\r\n        </el-select>\r\n        区/县:\r\n        <el-select v-model=\"ListQuery.county\"\r\n                   placeholder=\"请选择区/县\"\r\n                   @change=\"changeCounty\"\r\n                   class=\"region\">\r\n          <el-option v-for=\"item in countyList\"\r\n                     :key=\"item.id\"\r\n                     :label=\"item.name\"\r\n                     :value=\"item.id\">\r\n          </el-option>\r\n        </el-select>\r\n        <!-- <el-button type=\"primary\"\r\n                   class=\"printRight\"\r\n                   @click=\"print\">下载</el-button> -->\r\n      </div>\r\n      <el-table :data=\"list\"\r\n                tooltip-effect=\"dark\">\r\n        <!-- <el-table-column type=\"selection\"\r\n                         width=\"55\">\r\n        </el-table-column> -->\r\n        <!-- <el-table-column label=\"福利\"\r\n                         align=\"center\"\r\n                         prop=\"user_type\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"text\"\r\n                       v-if='scope.row.user_type==1'\r\n                       @click=\"details(scope.row)\">保险公司</el-button>\r\n            <span v-else>\r\n              {{scope.row.user_type==1 ? '保险公司' : scope.row.user_type==2 ? '主机厂' : '其他类型'}}\r\n            </span>\r\n          </template>\r\n        </el-table-column> -->\r\n        <el-table-column prop=\"ucp_company\"\r\n                         label=\"保险公司名称\"\r\n                         align=\"center\"></el-table-column>\r\n        <el-table-column prop=\"company\"\r\n                         label=\"维修厂名称\"\r\n                         align=\"center\"></el-table-column>\r\n        <!-- <el-table-column prop=\"phone\"\r\n                         label=\"联系电话\"\r\n                         align=\"center\"></el-table-column>\r\n        <el-table-column prop=\"leader\"\r\n                         label=\"负责人\"\r\n                         align=\"center\"></el-table-column> -->\r\n        <el-table-column label=\"地区\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-popover placement=\"top-start\"\r\n                        title=\"地区详情\"\r\n                        width=\"200\"\r\n                        trigger=\"hover\"\r\n                        :content=\"`${scope.row.province}${scope.row.city}${scope.row.county}`\">\r\n              <el-button type=\"text\"\r\n                         slot=\"reference\"\r\n                         class=\"ellipsis\">{{`${scope.row.province}${scope.row.city}${scope.row.county}`}}</el-button>\r\n            </el-popover>\r\n          </template>\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"复核数量\"\r\n                         align=\"center\"\r\n                         prop=\"num\">\r\n        </el-table-column>\r\n        <el-table-column label=\"操作\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"primary\"\r\n                       size=\"mini\"\r\n                       @click=\"details(scope.row)\">详情</el-button>\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n\r\n      <!-- 分页 -->\r\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\r\n           v-show=\"list && list.length>0\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n    <el-dialog title=\"详情\"\r\n               center\r\n               :visible.sync=\"detailsVisible\"\r\n               width=\"80%\"\r\n               @close='detailsPage=1,detailsCount=1'>\r\n      <el-table :data=\"detailsList\"\r\n                class=\"detailsTable\">\r\n        <el-table-column align=\"center\"\r\n                         label=\"施工照片\">\r\n          <template slot-scope=\"scope\">\r\n            <div>\r\n              <img v-for=\"(item,index) in scope.row.oil_photo\"\r\n                   :key=\"index\"\r\n                   :src=\"item\"\r\n                   width=\"50px\"\r\n                   height=\"50px\"\r\n                   ref=\"oil_photo\" />\r\n            </div>\r\n          </template>\r\n        </el-table-column>\r\n        <!-- <el-table-column align=\"center\"\r\n                         prop=\"ucp_company\"\r\n                         label=\"保险公司名称\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         prop=\"name\"\r\n                         label=\"车辆名称\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         prop=\"type\"\r\n                         label=\"车辆类型\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         prop=\"series\"\r\n                         label=\"车排量\">\r\n\r\n        </el-table-column> -->\r\n        <el-table-column align=\"center\"\r\n                         prop=\"plate\"\r\n                         label=\"车牌号\">\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         prop=\"rankers\"\r\n                         label=\"类型\">\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"老兵姓名\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.ranker==6 ? scope.row.old_name : '无'}}\r\n          </template>\r\n        </el-table-column>\r\n        <!-- <el-table-column align=\"center\"\r\n                         prop=\"vin\"\r\n                         label=\"vin码\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         prop=\"policy_num\"\r\n                         label=\"保单号\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.policy_num || '无'}}\r\n          </template>\r\n        </el-table-column> -->\r\n        <el-table-column align=\"center\"\r\n                         prop=\"\"\r\n                         label=\"保单图片/优待证\">\r\n          <template slot-scope=\"scope\">\r\n\r\n            <el-button type=\"text\"\r\n                       v-if=\"scope.row.pc_img && scope.row.pc_img.length>0\"\r\n                       @click=\"pcImgVisible=true,imgList=scope.row.pc_img,imgDetails()\">详情</el-button>\r\n            <span v-else>无</span>\r\n\r\n          </template>\r\n        </el-table-column>\r\n        <!-- <el-table-column align=\"center\"\r\n                         prop=\"start_time\"\r\n                         label=\"开始时间\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.start_time || '无'}}\r\n          </template>\r\n        </el-table-column> -->\r\n        <el-table-column align=\"center\"\r\n                         label=\"修改时间\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.audit_time | datetime}}\r\n          </template>\r\n        </el-table-column>\r\n\r\n        <el-table-column min-width=\"100px\"\r\n                         label=\"操作\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"primary\"\r\n                       size='mini'\r\n                       :loading=\"throughLoading[scope.$index]\"\r\n                       @click=\"through(scope.row,scope.$index)\">通过</el-button>\r\n            <el-button type=\"danger\"\r\n                       size='mini'\r\n                       :loading=\"rejecteLoading[scope.$index]\"\r\n                       @click=\"rejecte(scope.row,scope.$index)\">驳回</el-button>\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n      <div class=\"page_center\">\r\n        <paging :page-count=\"detailsCount\"\r\n                :page=\"detailsPage\"\r\n                @index=\"detailsPaging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n\r\n      <el-dialog center\r\n                 title=\"保单图片\"\r\n                 :visible.sync=\"pcImgVisible\"\r\n                 append-to-body\r\n                 width=\"25%\">\r\n        <div>\r\n          <img v-for=\"(item,index) in imgList\"\r\n               style=\"width:100px;height:100px;margin-right:20px\"\r\n               :key='index'\r\n               :src=\"item\"\r\n               ref=\"img\" />\r\n        </div>\r\n      </el-dialog>\r\n    </el-dialog>\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\nimport Viewer from '@/utils/Viewer'\r\nimport { getPro } from '@/utils'\r\nimport region from '@/components/common'\r\nexport default {\r\n  data () {\r\n    return {\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      page: 1,\r\n      pageCount: 1,\r\n      thCurId: '',\r\n      authList: [],\r\n      threeAuthList: [],\r\n      throughLoading: [],\r\n      rejecteLoading: [],\r\n      detailsVisible: false,\r\n      pcImgVisible: false,\r\n      multipleData: [],\r\n      detailsList: [],\r\n      detailsPage: 1,\r\n      detailsCount: 1,\r\n      currentDetails: {},\r\n      ListQuery: {\r\n        company: '',\r\n        ucp_company: '',\r\n        start_time: '',\r\n        end_time: '',\r\n        city: '',\r\n        county: '',\r\n        search: []\r\n      },\r\n      pickerSearch: \"\",\r\n      provincelist: [],\r\n      citylist: [],\r\n      countyList: [],\r\n      companyList: [], //保险公司列表\r\n      imgList: []\r\n    }\r\n  },\r\n  created () {\r\n    this.init();\r\n  },\r\n  watch: {\r\n    pickerSearch (val) {   //获取日期\r\n      if (val) {\r\n        this.ListQuery.start_time = val[0]\r\n        this.ListQuery.end_time = val[1]\r\n      } else {\r\n        this.ListQuery.start_time = ''\r\n        this.ListQuery.end_time = ''\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    GetCity: region.getCity,\r\n    GetCounty: region.getCounty,\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n    detailsPaging (e) {\r\n      this.detailsPage = e;\r\n      this.details(this.currentDetails)\r\n    },\r\n    init () {\r\n      this.$axios.post('admin/UserAudit/channelLists', Object.assign(this.ListQuery, { token: this.token, page: this.page, status: 0, pay_status: 1, review: 2 }))\r\n        .then(res => {\r\n          this.list = res.data.data.list;\r\n          this.pageCount = res.data.data.rows;\r\n        }).catch(err => { })\r\n    },\r\n    //获取保险公司\r\n    async getCompany () {\r\n      try {\r\n        const res = await this.$axios.post('admin/UserAudit/policyCompany', { token: this.token, type_channel: 1 })\r\n        this.companyList = res.data.data || []\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n    //获取全部省\r\n    async getProvince () {\r\n      try {\r\n        const res = await getPro();\r\n        this.provincelist = res;\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n    //获取市\r\n    getCity (id) {\r\n      this.page = 1\r\n      this.ListQuery.search['province'] = this.query(this.provincelist, id)\r\n      this.ListQuery.search['city'] = ''  //清空上一次所选的市\r\n      this.ListQuery.county = ''  //清空上一次所选的区/县\r\n      this.ListQuery.search['county_id'] = ''  //清空上一次所选的区/县id\r\n      this.GetCity(id).then(res => {\r\n        this.citylist = res\r\n      })\r\n      this.init()\r\n    },\r\n    //获取 区/县\r\n    getCounty (id) {\r\n      this.page = 1\r\n      this.ListQuery.search['city'] = this.query(this.citylist, id)\r\n      this.ListQuery.county = '' //清空上一次所选的区/县\r\n      this.ListQuery.search['county_id'] = '' //清空上一次所选的区/县id\r\n      this.GetCounty(id).then(res => {\r\n        this.countyList = res\r\n      })\r\n      this.init()\r\n    },\r\n    changeCounty (id) {\r\n      this.page = 1\r\n      this.ListQuery.search['county_id'] = id\r\n      this.init()\r\n    },\r\n    //通过 id 查询\r\n    query (array, id) {  //array 查询的数组  id 通过当前id查询array数组的数据\r\n      console.log(array)\r\n      let newArray = array.filter(item => {\r\n        return item.id == id\r\n      })\r\n      return newArray[0].name\r\n    },\r\n    async details (row) {  //详情\r\n      this.detailsVisible = true\r\n      this.currentDetails = row\r\n      try {\r\n        const res = await this.$axios.post(\"admin/UserAudit/details\", { token: this.token, sid: row.id, policy_id: row.policy_id, status: row.status, page: this.detailsPage, start_time: this.ListQuery.start_time, end_time: this.ListQuery.end_time })\r\n        this.detailsList = res.data.data.list || [];\r\n        this.detailsCount = res.data.data.rows;\r\n        this.$nextTick(() => {  //施工图放大\r\n          if (this.$refs.oil_photo && this.$refs.oil_photo.length > 0) {\r\n            Viewer(this.$refs.oil_photo)\r\n          }\r\n        })\r\n\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n\r\n    },\r\n    // handleSelectionChange (val) {  //全选/单选\r\n    //   let tmp = []\r\n    //   val.forEach(el => {\r\n    //     tmp.push({ ucp_id: el.ucp_id, uc_id: el.uc_id, user_type: el.user_type })\r\n    //   });\r\n\r\n    //   this.multipleData = tmp\r\n    // },\r\n    imgDetails () {  //保单图片详情\r\n      this.$nextTick(() => {\r\n        console.log(this.$refs)\r\n        const img = this.$refs.img\r\n        Viewer(img)\r\n      })\r\n    },\r\n    //通过\r\n    through (item, index) {\r\n\r\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\r\n   